@import './mix.less';
body {
    font-family: "PT Sans";
    color: #353535;
    margin: 0;
    overflow-x: hidden;
    ul {
        padding: 0;
        margin: 0;
        li {
            list-style: none;
        }
    }
    a {
        text-decoration: none;
        color: #353535;
    }
    p {
        margin: 0;
    }
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
        margin: 0;
    }
    img {
        max-width: 100%;
        height: auto;
    }
    .wrapper {
        max-width: 1266px;
        margin: auto;
    }
    header {
        .menu-top {
            padding: 13px 22px;
            box-shadow: 0px 4px 5px #DADADA;
            nav {
                display: flex;
                align-items: center;
                justify-content: space-between;
                .navigation {
                    display: flex;
                    align-items: center;
                    .logo {
                        margin-right: 42px;
                    }
                    ul {
                        display: flex;
                        li {
                            margin-right: 30px;
                            &:last-child {
                                margin-right: 0;
                            }
                            a {
                                font-size: 14px;
                                text-transform: capitalize;
                            }
                        }
                    }
                }
                .tool {
                    display: flex;
                    align-items: center;
                    .contacts {
                        text-align: right;
                        line-height: 17px;
                        margin-right: 14px;
                        a {
                            font-weight: 700;
                            font-size: 14px;
                        }
                        p {
                            font-size: 11px;
                            opacity: .4;
                        }
                    }
                    .call {
                        margin-right: 30px;
                        a {
                            padding: 9px 23px;
                            color: #fff;
                            background: #49C562;
                            border-radius: 5px;
                            box-shadow: 0px 4px 5px #DADADA;
                        }
                    }
                    .tools {
                        a {
                            margin-right: 29px;
                            &:last-child {
                                margin-right: 0;
                            }
                        }
                    }
                }
            }
        }
        .pre-menu {
            padding: 10px 0;
            .wrapper {
                max-width: 1080px;
                nav {
                    ul {
                        display: flex;
                        justify-content: space-between;
                        li {
                            font-size: 12px;
                            text-transform: capitalize;
                            a {
                                color: #7D7D7D;
                            }
                        }
                    }
                }
            }
        }
        .head-title {
            .bg("../img/bg.jpg");
            padding: 70px 0;
            position: relative;
            .wrapper {
                .content-head {
                    display: flex;
                    position: relative;
                    .title {
                        display: inline-block;
                        width: 420px;
                        text-align: center;
                        h1 {
                            font-family: "Lora";
                            font-weight: 700;
                            font-size: 48px;
                        }
                        .owl-dots {
                            display: flex;
                            justify-content: center;
                            margin-top: 20px;
                            .owl-dot {
                                display: block;
                                background: transparent;
                                border-radius: 30px;
                                border: 2px solid #3F3F3F;
                                padding: 0;
                                margin-right: 10px;
                                &:last-child {
                                    margin-right: 0;
                                }
                                span {
                                    display: block;
                                    width: 6px;
                                    height: 6px;
                                }
                            }
                        }
                    }
                    .slider {
                        top: -30px;
                        right: -1690px;
                        position: absolute;
                    }
                }
            }
        }
    }
    .content {
        padding-top: 40px;
        padding-bottom: 60px;
        .wrapper {
            max-width: 1110px;
            .items {
                display: flex;
                flex-wrap: wrap;
                .card {
                    margin-top: 15px;
                    margin-bottom: 15px;
                    padding: 17px 22px;
                    box-sizing: border-box;
                    height: 264px;
                    width: 198px;
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;
                    border: 1px solid rgba(125, 125, 125, .3);
                    border-radius: 5px;
                    position: relative;
                    margin-right: 30px;
                    &:nth-child(5) {
                        margin-right: 0;
                    }
                    &:nth-child(10) {
                        margin-right: 0;
                    }
                    &:nth-child(15) {
                        margin-right: 0;
                    }
                    &:nth-child(20) {
                        margin-right: 0;
                    }
                    img {
                        z-index: 0;
                        top: 0;
                        right: 0;
                        position: absolute;
                    }
                    .title {
                        z-index: 1;
                        h2 {
                            color: #3F3F3F;
                            font-size: 14px;
                            font-weight: 700;
                        }
                    }
                    .under {
                        z-index: 1;
                        p {
                            font-size: 12px;
                            display: inline-block;
                            color: #fff;
                            padding: 1px 6px;
                            border-radius: 3px;
                            &.yellow {
                                background: #EF7F1A;
                                text-transform: capitalize;
                            }
                            &.green {
                                background: #49C562;
                                text-transform: uppercase;
                            }
                        }
                        span {
                            display: block;
                            font-size: 14px;
                            color: #989898;
                        }
                    }
                }
            }
        }
    }
    footer {
        .pre-footer {
            padding: 22px 0;
            background: #383838;
            .wrapper {
                max-width: 1110px;
                .footer-nav {
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    ul {
                        display: flex;
                        align-items: center;
                        li {
                            margin-right: 35px;
                            &:last-child {
                                margin-right: 0;
                            }
                            a {
                                text-transform: capitalize;
                                color: #fff;
                                font-size: 14px;
                            }
                        }
                    }
                    .soc {
                        display: flex;
                        a {
                            display: flex;
                            align-items: center;
                            margin-right: 20px;
                            &:last-child {
                                margin-right: 0;
                            }
                        }
                    }
                }
            }
        }
        .footer {
            padding: 33px 0;
            background: #3F3F3F;
            .wrapper {
                max-width: 1110px;
                .inf {
                    display: flex;
                    justify-content: space-between;
                    .information {
                        display: flex;
                        align-items: center;
                        .logo {
                            margin-right: 50px;
                        }
                        .add {
                            display: flex;
                            margin-right: 30px;
                            p {
                                color: #fff;
                                opacity: .5;
                            }
                        }
                        .contact {
                            display: flex;
                            p {
                                color: #fff;
                                opacity: .5;
                            }
                        }
                    }
                    .label {
                        p {
                            text-align: right;
                            color: #fff;
                            opacity: .5;
                        }
                    }
                }
            }
        }
    }
}