body {
  font-family: "PT Sans";
  color: #353535;
  margin: 0;
  overflow-x: hidden;
}
body ul {
  padding: 0;
  margin: 0;
}
body ul li {
  list-style: none;
}
body a {
  text-decoration: none;
  color: #353535;
}
body p {
  margin: 0;
}
body h1,
body h2,
body h3,
body h4,
body h5,
body h6 {
  margin: 0;
}
body img {
  max-width: 100%;
  height: auto;
}
body .wrapper {
  max-width: 1266px;
  margin: auto;
}
body header .menu-top {
  padding: 13px 22px;
  box-shadow: 0px 4px 5px #DADADA;
}
body header .menu-top nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
body header .menu-top nav .navigation {
  display: flex;
  align-items: center;
}
body header .menu-top nav .navigation .logo {
  margin-right: 42px;
}
body header .menu-top nav .navigation ul {
  display: flex;
}
body header .menu-top nav .navigation ul li {
  margin-right: 30px;
}
body header .menu-top nav .navigation ul li:last-child {
  margin-right: 0;
}
body header .menu-top nav .navigation ul li a {
  font-size: 14px;
  text-transform: capitalize;
}
body header .menu-top nav .tool {
  display: flex;
  align-items: center;
}
body header .menu-top nav .tool .contacts {
  text-align: right;
  line-height: 17px;
  margin-right: 14px;
}
body header .menu-top nav .tool .contacts a {
  font-weight: 700;
  font-size: 14px;
}
body header .menu-top nav .tool .contacts p {
  font-size: 11px;
  opacity: 0.4;
}
body header .menu-top nav .tool .call {
  margin-right: 30px;
}
body header .menu-top nav .tool .call a {
  padding: 9px 23px;
  color: #fff;
  background: #49C562;
  border-radius: 5px;
  box-shadow: 0px 4px 5px #DADADA;
}
body header .menu-top nav .tool .tools a {
  margin-right: 29px;
}
body header .menu-top nav .tool .tools a:last-child {
  margin-right: 0;
}
body header .pre-menu {
  padding: 10px 0;
}
body header .pre-menu .wrapper {
  max-width: 1080px;
}
body header .pre-menu .wrapper nav ul {
  display: flex;
  justify-content: space-between;
}
body header .pre-menu .wrapper nav ul li {
  font-size: 12px;
  text-transform: capitalize;
}
body header .pre-menu .wrapper nav ul li a {
  color: #7D7D7D;
}
body header .head-title {
  background: transparent url("../img/bg.jpg") no-repeat center center;
  background-size: cover;
  padding: 70px 0;
  position: relative;
}
body header .head-title .wrapper .content-head {
  display: flex;
  position: relative;
}
body header .head-title .wrapper .content-head .title {
  display: inline-block;
  width: 420px;
  text-align: center;
}
body header .head-title .wrapper .content-head .title h1 {
  font-family: "Lora";
  font-weight: 700;
  font-size: 48px;
}
body header .head-title .wrapper .content-head .title .owl-dots {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}
body header .head-title .wrapper .content-head .title .owl-dots .owl-dot {
  display: block;
  background: transparent;
  border-radius: 30px;
  border: 2px solid #3F3F3F;
  padding: 0;
  margin-right: 10px;
}
body header .head-title .wrapper .content-head .title .owl-dots .owl-dot:last-child {
  margin-right: 0;
}
body header .head-title .wrapper .content-head .title .owl-dots .owl-dot span {
  display: block;
  width: 6px;
  height: 6px;
}
body header .head-title .wrapper .content-head .slider {
  top: -30px;
  right: -1690px;
  position: absolute;
}
body .content {
  padding-top: 40px;
  padding-bottom: 60px;
}
body .content .wrapper {
  max-width: 1110px;
}
body .content .wrapper .items {
  display: flex;
  flex-wrap: wrap;
}
body .content .wrapper .items .card {
  margin-top: 15px;
  margin-bottom: 15px;
  padding: 17px 22px;
  box-sizing: border-box;
  height: 264px;
  width: 198px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border: 1px solid rgba(125, 125, 125, 0.3);
  border-radius: 5px;
  position: relative;
  margin-right: 30px;
}
body .content .wrapper .items .card:nth-child(5) {
  margin-right: 0;
}
body .content .wrapper .items .card:nth-child(10) {
  margin-right: 0;
}
body .content .wrapper .items .card:nth-child(15) {
  margin-right: 0;
}
body .content .wrapper .items .card:nth-child(20) {
  margin-right: 0;
}
body .content .wrapper .items .card img {
  z-index: 0;
  top: 0;
  right: 0;
  position: absolute;
}
body .content .wrapper .items .card .title {
  z-index: 1;
}
body .content .wrapper .items .card .title h2 {
  color: #3F3F3F;
  font-size: 14px;
  font-weight: 700;
}
body .content .wrapper .items .card .under {
  z-index: 1;
}
body .content .wrapper .items .card .under p {
  font-size: 12px;
  display: inline-block;
  color: #fff;
  padding: 1px 6px;
  border-radius: 3px;
}
body .content .wrapper .items .card .under p.yellow {
  background: #EF7F1A;
  text-transform: capitalize;
}
body .content .wrapper .items .card .under p.green {
  background: #49C562;
  text-transform: uppercase;
}
body .content .wrapper .items .card .under span {
  display: block;
  font-size: 14px;
  color: #989898;
}
body footer .pre-footer {
  padding: 22px 0;
  background: #383838;
}
body footer .pre-footer .wrapper {
  max-width: 1110px;
}
body footer .pre-footer .wrapper .footer-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
body footer .pre-footer .wrapper .footer-nav ul {
  display: flex;
  align-items: center;
}
body footer .pre-footer .wrapper .footer-nav ul li {
  margin-right: 35px;
}
body footer .pre-footer .wrapper .footer-nav ul li:last-child {
  margin-right: 0;
}
body footer .pre-footer .wrapper .footer-nav ul li a {
  text-transform: capitalize;
  color: #fff;
  font-size: 14px;
}
body footer .pre-footer .wrapper .footer-nav .soc {
  display: flex;
}
body footer .pre-footer .wrapper .footer-nav .soc a {
  display: flex;
  align-items: center;
  margin-right: 20px;
}
body footer .pre-footer .wrapper .footer-nav .soc a:last-child {
  margin-right: 0;
}
body footer .footer {
  padding: 33px 0;
  background: #3F3F3F;
}
body footer .footer .wrapper {
  max-width: 1110px;
}
body footer .footer .wrapper .inf {
  display: flex;
  justify-content: space-between;
}
body footer .footer .wrapper .inf .information {
  display: flex;
  align-items: center;
}
body footer .footer .wrapper .inf .information .logo {
  margin-right: 50px;
}
body footer .footer .wrapper .inf .information .add {
  display: flex;
  margin-right: 30px;
}
body footer .footer .wrapper .inf .information .add p {
  color: #fff;
  opacity: 0.5;
}
body footer .footer .wrapper .inf .information .contact {
  display: flex;
}
body footer .footer .wrapper .inf .information .contact p {
  color: #fff;
  opacity: 0.5;
}
body footer .footer .wrapper .inf .label p {
  text-align: right;
  color: #fff;
  opacity: 0.5;
}
